<template>
    <div>
        <h2>
            <img src="../assets/logo.png">
            普瑞盛SAS宏管理平台 V1.0</h2>
        <div class="left_menu">
            <ul>
                <li v-for="(item,index) in menuData" :key="item.id" 
                v-bind:class="{ active:item.checked}" 
                v-on:click="fetchData(item,index)">{{item.macroName}}</li>
            </ul>
        </div>
        <div class="right_content">
            <router-view></router-view>
            <!-- <right-content v-bind:macro-name="name"></right-content> -->
        </div>
    </div>
</template>
<style scoped>
    h2{
        position: fixed;
        width: 100%;
        height: 55px;
        color: #fff;
        text-align: center;
        background: #0b70c1;
        margin: 0;
        line-height: 55px;
        top: 0;
    }
    h2 img{
        width: 178px;
        float: left;
    }
    .left_menu{
        width: 120px;
        background: #efefef;
        height: 100%;
        overflow-y: scroll;
        position: fixed;
        top: 55px;
        z-index: 100;
        padding-top: 30px;
    }
    .left_menu ul li{
        height: 40px;
        line-height: 40px;
        text-align: center;
        cursor: pointer;
    }
    .left_menu ul li:hover{
        background: #17609a;
        color: #fff;
    }
    .left_menu ul li.active{
        background: #17609a;
        color: #fff;
    }
    .right_content{
        /* width: 100%; */
        margin-top: 55px;
        margin-left: 120px;
        padding: 30px;
    }
</style>
<script>
import menu from '../../static/data/menu.js'
export default {
    data(){
        return{
            menuData:[],
        }
    },
    mounted(){
        this.menuData = menu.result;
        for(let i of this.menuData){
            i.checked = false;
        }
        this.menuData[0].checked = true;
        this.$router.push({name:'detail', params:{name:this.menuData[0].macroName}});
    },
    methods:{
        fetchData: function(item, index){
            this.menuData = this.menuData.map((menuItem, menuItemIndex) => ({...menuItem, checked: menuItemIndex == index}))
            this.$router.push({name:'detail', params:{name:item.macroName}})
        }
    },
    // components:{
    //     rightContent
    // }
}
</script>





