<template>
    <div>
        <router-link class="return_prev" :to="{name:'detail', params:{name:name}}">返回上一页》</router-link>
        <ul>
            <li v-for="item in dataList">{{item.text}}</li>
        </ul>
        <textarea name="" id="" cols="100" rows="7" placeholder="请输入相关问题反馈" v-model="problemData">

        </textarea>
        <div>
            <button class="btn" v-on:click='submit()'>提交</button>
        </div>
    </div>
</template>
<style scoped>
    ul{
        margin: 20px;
    }
    ul li{
        list-style-type: disc;
        margin-bottom: 15px;
    }
    .btn{
        margin-left: 582px;
        cursor: pointer;
    }
</style>
<script>
export default {

    data(){
        return{
            dataList: [],
            name: this.$route.params.name,
            problemData: ''
        }
    },
    mounted(){
        this.getList();
    },
    methods:{
        getList: function(){
            let _that = this;
            AJAX.default.ajaxPost(
                API.default.problemList,
                {
                    name: this.name
                },
                function(result){
                    let data = result.data;
                    if(data.status == 200){
                        _that.dataList = data.result;
                    }
                }
            )
        },
        submit: function(){
            let _that = this;
            if(!this.problemData){
                alert('反馈的问题不能为空');
                return false;
            }
            AJAX.default.ajaxPost(
                API.default.addQuestion,
                {
                    name: this.name,
                    text: this.problemData
                },
                function(result){
                    let data = result.data;
                    if(data.status == 200){
                        _that.problemData = '';
                        _that.getList();
                        alert('提交成功');
                    }
                }
            )
        }
    }
}
</script>





